.command {
  cursor: pointer;
  border: 3px solid var(--nav-color);
  box-shadow: 0 2px 3px var(--nav-color);
  width: 50%;
  margin: auto;
  margin-bottom: 10px;
  padding: 10px;
  outline: none;
}

.command * {
  pointer-events: none;
}

.description {
  cursor: text;
  overflow: hidden;
  max-height: 0;
  transition: all 0.2s;
  background-color: var(--nav-color);
  box-shadow: inset 0 0 5px black;
}

.active .description {
  max-height: 1000px;
  margin-top: 10px;
  padding: 5px;
}

.description ul {
  text-align: left;
}

.detail {
  margin: 10px;
}

pre {
  display: inline;
}

@media screen and (max-width: 890px) {
  .command {
    width: 70%;
  }
}

@media screen and (max-width: 750px) {
  .command {
    width: 100%;
  }
}
