@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
nav {
  width: 100%;
  background-color: var(--nav-color);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  height: 60px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  box-shadow: 0 0 4px black;
  opacity: 0;
}

nav a {
  color: var(--text-color);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

nav a:hover {
  color: #f1c40f;
}

.buttons {
  width: 50%;
  height: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
}

.buttons a {
  display: block;
  height: 100%;
  font-weight: bold;
  color: var(--text-color);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.buttons a:hover {
  color: #f1c40f;
}

#premium {
  color: #f1c40f;
}

#premium:hover {
  background-color: #f1c40f;
  color: var(--nav-color);
}

#burger-container {
  display: none;
}

#burger {
  font-size: 27px;
}

@media screen and (max-width: 800px) {
  nav {
    justify-content: space-between;
    padding-left: 30px;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    padding-right: 30px;
  }
  .buttons {
    display: none;
  }

  #burger-container {
    display: block;
  }

  .open {
    align-items: flex-start;
    flex-direction: column;
    justify-content: flex-start;
    height: auto;
  }

  .open .buttons {
    display: block;
    width: 100%;
    margin-top: 70px;
    font-size: 20px;
  }

  .open .buttons * {
    padding: 30px;
  }

  .open h1 {
    position: absolute;
    left: 30px;
    top: 17px;
  }

  .open #burger-container {
    position: absolute;
    right: 30px;
    top: 17px;
  }
}

.mom {
  width: auto;
  height: 320px;
  border-radius: 50%;
  box-shadow: 0 1px 5px black;
  opacity: 0;
}

.homepage {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.textcontainer {
  display: flex;
  flex-direction: column;
  width: 35%;
  text-align: left;
  opacity: 0;
}

.textcontainer h1 {
  font-size: 50px;
  margin-left: 0;
  margin-bottom: 20px;
}
.momh1 {
  cursor: default;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
  color: #f1c40f;
}

.textcontainer p {
  font-size: 30px;
}

#add {
  margin: auto;
  position: relative;
  margin-top: 100px;
  background-color: #f1c40f;
  border: none;
  color: var(--nav-color);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px black;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  opacity: 0;
  font-size: 1px;
}

#add:hover {
  cursor: pointer;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

@media screen and (max-width: 768px) {
  .homepage {
    flex-direction: column;
  }

  .mom {
    height: 245px;
    margin-bottom: 40px;
  }

  #add {
    margin-top: 70px;
  }

  .textcontainer {
    width: 80%;
    text-align: center;
  }
}

.info {
  width: 80%;
  margin: auto;
  line-height: 33px;
  margin-bottom: 10px;
}

.info p {
  font-size: 22px;
}

.about h1 {
  margin-bottom: 20px;
}

.carousel {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}

.carousel * {
  border-radius: 10px;
}

@media screen and (max-width: 438px) {
  .info p {
    text-align: justify;
  }

  .info h1 {
    line-height: 40px;
  }
}

.commandscontainer {
  opacity: 0;
  width: 80%;
  height: 85vh;
  margin: auto;
  overflow-y: scroll;
}

::-webkit-scrollbar {
  width: 0px;
}

.commandscontainer ul {
  list-style: none;
}

.commandscontainer h1 {
  margin-bottom: 20px;
}

.command {
  cursor: pointer;
  border: 3px solid var(--nav-color);
  box-shadow: 0 2px 3px var(--nav-color);
  width: 50%;
  margin: auto;
  margin-bottom: 10px;
  padding: 10px;
  outline: none;
}

.command * {
  pointer-events: none;
}

.description {
  cursor: text;
  overflow: hidden;
  max-height: 0;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  background-color: var(--nav-color);
  box-shadow: inset 0 0 5px black;
}

.active .description {
  max-height: 1000px;
  margin-top: 10px;
  padding: 5px;
}

.description ul {
  text-align: left;
}

.detail {
  margin: 10px;
}

pre {
  display: inline;
}

@media screen and (max-width: 890px) {
  .command {
    width: 70%;
  }
}

@media screen and (max-width: 750px) {
  .command {
    width: 100%;
  }
}

#searchbar {
  background-color: var(--background-color);
  border: none;
  border-bottom: 4px solid var(--nav-color);
  font-size: 30px;
  width: 50%;
  padding: 10px;
  color: var(--text-color);
  -webkit-transition: all 0.15s;
  transition: all 0.15s;
  caret-color: var(--text-color);
  outline: none;
  margin: 20px auto;
}

::-webkit-input-placeholder {
  color: #95a5a6;
}

::-moz-placeholder {
  color: #95a5a6;
}

:-ms-input-placeholder {
  color: #95a5a6;
}

::-ms-input-placeholder {
  color: #95a5a6;
}

::placeholder {
  color: #95a5a6;
}

#searchbar:focus,
#searchbar:hover {
  font-size: 32px;
}

@media screen and (max-width: 890px) {
  #searchbar {
    font-size: 20px;
    width: 100%;
  }
  #searchbar:focus,
  #searchbar:hover {
    font-size: 25px;
  }
}

@media screen and (max-width: 600px) {
  #searchbar {
    font-size: 13px;
  }
  #searchbar:focus,
  #searchbar:hover {
    font-size: 15px;
  }
}

.tags {
  width: 50%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.tag {
  padding: 14px;
  border-radius: 5px;
  border: none;
  background-color: var(--nav-color);
  color: var(--text-colot);
  font-size: 15px;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  margin: 4px;
}

.tag:hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.selected {
  background-color: #2980b9;
}

@media screen and (max-width: 890px) {
  .tags {
    width: 100%;
  }
  .tag:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  --nav-color: #2c3e50;
  --background-color: #34495e;
  --text-color: #ecf0f1;
  font-family: "Montserrat";
  background-color: var(--background-color);
}

a {
  text-decoration: none;
}

.App {
  color: var(--text-color);
  text-align: center;
}

.container {
  margin-top: 40px;
  width: 100%;
}

